
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import { mapMutations } from "vuex";
export default {
    methods: {
        request(url, data) {
            return new Promise((resolve, reject) => {
                uni.request({
                    url,
                    header: {
                        auth: uni.getStorageSync("session_key")
                    },
                    data,
                    method: "POST",
                    success: res => {
                        resolve(res);
                    },
                    fail() {
                        reject();
                    }
                });
            });
        },
        ...mapMutations(["login"])
    },
    onLaunch: function() {
        let userInfo = uni.getStorageSync("userInfo") || "";
        if (userInfo.id) {
            //更新登陆状态
            uni.getStorage({
                key: "userInfo",
                success: res => {
                    this.login(res.data);
                }
            });
        }
    },
    onShow: function(options) {
        console.log("App Show");

        let livePlayer = requirePlugin("live-player-plugin");

        // 分享卡片入口场景才调用getShareParams接口获取以下参数
        if (
            options.scene == 1007 ||
            options.scene == 1008 ||
            options.scene == 1044
        ) {
            livePlayer
                .getShareParams()
                .then(res => {
                    console.log("get room id", res.room_id); // 房间号
                    console.log("get openid", res.openid); // 用户openid
                    console.log("get share openid", res.share_openid); // 分享者openid，分享卡片进入场景才有
                    console.log("get custom params", res.custom_params); // 开发者在跳转进入直播间页面时，页面路径上携带的自定义参数，这里传回给开发者
                })
                .catch(err => {
                    console.log("get share params", err);
                });
        }
    },
    onHide: function() {
        console.log("App Hide");
    }
};
